import { createRouter, createWebHistory } from 'vue-router'

const router = createRouter({
  history: createWebHistory(import.meta.env.BASE_URL),
  routes: [
    { path: '/', name: 'home', component: () => import('@/pages/Home.vue'), meta: { title: 'Incio  Andr Franceschini' } },
    { path: '/about', name: 'about', component: () => import('@/pages/About.vue'), meta: { title: 'Sobre  Andr Franceschini' } },
    { path: '/projects', name: 'projects', component: () => import('@/pages/Projects.vue'), meta: { title: 'Projetos  Andr Franceschini' } },
    { path: '/projects/:slug', name: 'project', component: () => import('@/pages/Project.vue'), meta: { title: 'Projeto  Andr Franceschini' } },
    { path: '/contact', name: 'contact', component: () => import('@/pages/Contact.vue'), meta: { title: 'Contato  Andr Franceschini' } },
    { path: '/:pathMatch(.*)*', name: 'not-found', component: () => import('@/pages/NotFound.vue'), meta: { title: 'Pgina no encontrada' } },
  ],
  scrollBehavior() {
    return { top: 0 }
  },
})

router.afterEach((to) => {
  const title = to.meta?.title as string | undefined
  if (title) document.title = title
})

export default router

